.drawflow,
.drawflow .parent-node {
  position: relative;
}

.parent-drawflow {
  display: flex;
  overflow: hidden;
  touch-action: none;
  outline: 0;
}

.drawflow {
  width: 100%;
  height: 100%;
  user-select: none;
}

.drawflow .drawflow-node:hover {
  cursor: move;
}

.drawflow .drawflow-node .inputs,
.drawflow .drawflow-node .outputs {
  width: 0;
}

.drawflow .drawflow-node .drawflow_content_node {
  width: 100%;
  display: block;
}

.drawflow .drawflow-node .input,
.drawflow .drawflow-node .output {
  position: relative;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 50%;
  border: 2px solid #000;
  cursor: crosshair;
  z-index: 1;
  margin-bottom: 5px;
}

.drawflow .drawflow-node .input {
  left: -27px;
  top: 2px;
  background: #ff0;
}

.drawflow .drawflow-node .output {
  right: -3px;
  top: 2px;
}

.drawflow svg {
  z-index: 0;
  position: absolute;
  overflow: visible !important;
}

.drawflow .connection {
  position: absolute;
  transform: translate(9999px, 10000px);
}

.drawflow .connection .main-path {
  fill: none;
  stroke-width: 5px;
  stroke: var(--border-color);
  transform: translate(-9999px, -9999px);
}

.drawflow .connection .main-path:hover {
  stroke: #1266ab;
  cursor: pointer;
}

.drawflow .connection .main-path.selected {
  stroke: #43b993;
}

.drawflow .connection .point {
  cursor: move;
  stroke: #000;
  stroke-width: 2;
  fill: #fff;
  transform: translate(-9999px, -9999px);
}

.drawflow .connection .point.selected,
.drawflow .connection .point:hover {
  fill: #1266ab;
}

.drawflow .main-path {
  fill: none;
  stroke-width: 5px;
  stroke: var(--border-color);
}

.drawflow .selectbox {
  z-index: 3;
  position: absolute;
  transform: translate(9999px, 9999px);
}

.drawflow .selectbox rect {
  fill: #00f;
  opacity: 0.5;
  stroke: #ff0;
  stroke-width: 5;
  stroke-opacity: 0.5;
  transform: translate(-9999px, -9999px);
}

.drawflow-delete {
  position: absolute;
  display: block;
  width: 30px;
  height: 30px;
  background: #000;
  color: #fff;
  z-index: 4;
  border: 2px solid #fff;
  line-height: 30px;
  font-weight: 700;
  text-align: center;
  border-radius: 50%;
  font-family: monospace;
  cursor: pointer;
}

.drawflow > .drawflow-delete {
  margin-left: -15px;
  margin-top: 15px;
}

.parent-node .drawflow-delete {
  right: -15px;
  top: -15px;
}

.popupWrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.6);
  z-index: 10;
  top: 0;
  left: 0;
}
.popupWrapper .popupContainer {
  width: 70%;
  color: #666;
  min-height: 200px;
  max-height: 400px;
  background: white;
  margin: 100px auto;
  position: relative;
  padding: 10px;
  overflow: auto;
}

.popupWrapper .popupContainer h3 {
  margin: 0;
}

.popupWrapper .closeIcon:before,
.popupWrapper .closeIcon:after {
  position: absolute;
  right: 10px;
  top: 10px;
  height: 2px;
  width: 15px;
  background: #444;
}

.popupWrapper .closeIcon {
  width: 25px;
  height: 25px;
  cursor: pointer;
  border-radius: 10px;
  border: 1px solid #ddd;
  position: relative;
  display: inline-block;
}

.popupWrapper .closeIcon:before,
.popupWrapper .closeIcon:after {
  content: ' ';
  right: 5px;
  top: 12px;
  transform: rotate(45deg);
}

.popupWrapper .closeIcon:after {
  transform: rotate(-45deg);
}
.reset-button {
  position: relative;
  top: auto;
  right: auto;
  left: auto;
  bottom: auto;
  float: none;
}
